@keyframes spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 10px solid #87b1ff; /* Light grey */
  border-top: 10px solid #948f8f; /* Black */
  border-radius: 50%;
  animation: spinner 1.5s linear infinite;
}
